<template>
  <div style="text-align: right;margin-top: 16px;">
    <el-pagination
      prev-text="上一页"
      next-text="下一页"
      @size-change="sizeChange"
      @current-change="currentChange"
      :current-page="currentPage"
      :page-sizes="pageSizes"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'BasePagination',
  props: {
    total: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      pageSizes: [5, 10, 20, 25],
      currentPage: 1,
      pageSize: 5
    }
  },
  computed: {
  },
  methods: {
    sizeChange (size) {
      this.pageSize = size
      this.currentPage = 1
      this.dataChange(size, this.currentPage)
    },
    currentChange (current) {
      this.currentPage = current
      this.dataChange(this.pageSize, current)
    },
    dataChange (size, page) {
      this.$emit('dataChange', size, page)
    }
  }
}
</script>

<style scoped>
</style>



